            top: photo.top + '%',
            left: photo.left + '%',
            width: photo.size + 'px',
            height: photo.size + 'px',
            opacity: photo.loaded ? photo.opacity : 0,
            transform: `rotate(${photo.rotate}deg)`,
            animationDelay: photo.delay + 's',
            transition: 'opacity 0.5s ease-in-out'
