        require('@/assets/club/bc/IMG_0420.webp'),
        require('@/assets/club/bc/IMG_0429.webp'),
        require('@/assets/club/bc/IMG_7279.webp'),
        require('@/assets/club/bc/IMG_7280.webp'),
        require('@/assets/club/bc/IMG_7281.webp'),
        require('@/assets/club/bc/IMG_7284.webp'),
        require('@/assets/club/bc/IMG_8190.webp'),
        
        // club/su目录图片
        require('@/assets/club/su/IMG_5240.webp'),
        require('@/assets/club/su/IMG_6197.webp'),
        require('@/assets/club/su/IMG_6266.webp'),
        require('@/assets/club/su/IMG_7365.webp'),
        
        // projects目录精选图片
        require('@/assets/projects/Campus Publicity & Art/IMG_5292.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_5643.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_6025.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_7364.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_8282.webp'),
        require('@/assets/projects/Campus Publicity & Art/IMG_9134.webp'),
        require('@/assets/projects/HiMCM/IMG_8226.webp'),
        require('@/assets/projects/HiMCM/IMG_8237.webp'),
        require('@/assets/projects/IMMC/3be683d85e352e33bde75a5ddd70afde.webp'),
        require('@/assets/projects/IMMC/IMG_2877.webp'),
        require('@/assets/projects/IMMC/IMG_3782.webp'),
        require('@/assets/projects/IMMC/IMG_5598.webp'),
        require('@/assets/projects/NextGen/WechatIMG11.webp'),
        require('@/assets/projects/NextGen/WechatIMG12.webp'),
        require('@/assets/projects/NextGen/WechatIMG13.webp'),
        require('@/assets/projects/NextGen/WechatIMG14.webp'),
        require('@/assets/projects/No. 80 Green Garden/25a08d56d56453a5098f76199e7be792.webp'),
        require('@/assets/projects/No. 80 Green Garden/IMG_0458.webp'),
        require('@/assets/projects/No. 80 Green Garden/IMG_8857.webp'),
        require('@/assets/projects/PBL Targeted Lung Cancer Treatment/59a747dd43d998ca3c464db4d093dfd1.webp'),
        require('@/assets/projects/PBL Targeted Lung Cancer Treatment/IMG_4332.webp'),
        require('@/assets/projects/PBL Targeted Lung Cancer Treatment/IMG_4564.webp'),
        require('@/assets/projects/Renewable Ion-Exchange Power System (SuperSMEst)/57a977f672c5b0c650360c4bbde4d5dd.webp'),
        
        // about图片
        require('@/assets/about/WechatIMG27.webp'),
        
        // contact图片
        require('@/assets/contact/contact.webp')
      ];
      
      // 分批逐步加载图片
      const batchSize = 5; // 每批加载5张图片
      let currentIndex = 0;
      
      const loadNextBatch = () => {
        const endIndex = Math.min(currentIndex + batchSize, this.totalImagesCount);
        
        for (let i = currentIndex; i < endIndex; i++) {
          const photoIndex = i;
          const url = photoUrls[i % photoUrls.length];
          
          const img = new Image();
          img.onload = () => {
            // 图片加载完成后更新对应的照片数据
            this.photos[photoIndex].url = url;
            this.photos[photoIndex].loaded = true;
            
            // 更新加载进度
            this.loadedImagesCount++;
            this.loadingProgress = Math.round((this.loadedImagesCount / this.totalImagesCount) * 100);
            
            // 检查是否所有图片都已加载
            this.checkAllImagesLoaded();
          };
          
          img.onerror = () => {
            // 如果某张图片加载失败，也标记为已加载
            this.photos[photoIndex].loaded = true;
            this.loadedImagesCount++;
            this.loadingProgress = Math.round((this.loadedImagesCount / this.totalImagesCount) * 100);
            this.checkAllImagesLoaded();
          };
          
          img.src = url;
        }
        
        currentIndex = endIndex;
        
        // 如果还有图片需要加载，继续下一批
        if (currentIndex < this.totalImagesCount) {
          setTimeout(loadNextBatch, 200); // 每批间隔200ms，避免一次性加载过多
        }
      };
      
      // 开始加载第一批图片
      loadNextBatch();
    },
    
    startPhotoAnimation() {
      setInterval(() => {
        // 随机选择一些照片消失
        this.photos.forEach(photo => {
          if (Math.random() < 0.02) { // 2%的概率切换可见性
            photo.visible = !photo.visible;
          }
        });
      }, 1000); // 每秒检查一次
    },
    startTypewriter() {
      let text = '';
      let charIndex = 0;
      
      const type = () => {
        if (charIndex < this.fullText.length) {
          text += this.fullText.charAt(charIndex);
          this.displayedText = text;
          charIndex++;
          setTimeout(type, this.typeSpeed);
        } else {
          setTimeout(this.erase, this.pauseTime);
        }
      };
      
      type();
    },
    erase() {
      let text = this.displayedText;
      
      const eraseChar = () => {
        if (text.length > 0) {
          text = text.substring(0, text.length - 1);
          this.displayedText = text;
          setTimeout(eraseChar, this.eraseSpeed);
        } else {
          setTimeout(this.startTypewriter, 500);
        }
      };
      
      eraseChar();
    },
    

    
    checkAllImagesLoaded() {
      if (this.loadedImagesCount >= this.totalImagesCount) {
        const elapsedTime = Date.now() - this.loadingStartTime;
        const remainingTime = Math.max(0, this.minLoadingTime - elapsedTime);
        
        if (remainingTime > 0) {
          // 如果图片加载完成但时间不足3秒，等待剩余时间
          setTimeout(() => {
            this.finishLoading();
          }, remainingTime);
        } else {
          this.finishLoading();
        }
      }
    },
    
    finishLoading() {
      // 先触发淡出效果
      this.isFadingOut = true;
      
      // 等待淡出动画完成后再切换界面
      setTimeout(() => {
        this.allImagesLoaded = true;
        this.$emit('imagesLoaded');
        
  // 彩纸特效默认保持关闭；如果需要可手动调用 enableMouseTrail() 或 startConfetti()
        
        // 延迟启动打字机动画，避免与照片墙动画同时开始
        setTimeout(() => {
          this.startTypewriter();
        }, 500);
        
        if (this.loadingTimer) {
          clearTimeout(this.loadingTimer);
        }
      }, 800); // 淡出动画持续时间
    },
    
