        require('@/assets/art/Sketch 3.webp'),
        require('@/assets/art/Sketch 4.webp'),
        require('@/assets/art/Sketch 5.webp'),
        require('@/assets/art/Sketch 6.webp'),
        require('@/assets/art/Sketch 7.webp'),
        require('@/assets/art/Sketch 8.webp'),
        require('@/assets/art/Still Life in Color 1.webp'),
        require('@/assets/art/Still Life in Color 2.webp'),
        require('@/assets/art/Still Life in Color 3.webp'),
        require('@/assets/art/Still Life in Color 4.webp'),
        require('@/assets/art/Still Life in Color 5.webp'),
        require('@/assets/art/Still Life in Color 6.webp'),
        
        // club/bc目录图片
        require('@/assets/club/bc/3f81876bba27a94fa7b999fcb41331b9.webp'),
        require('@/assets/club/bc/5362fe495b2d6c6b399198a12750c105.webp'),
        require('@/assets/club/bc/7bb8a701446f0358734f1391c5c11746.webp'),
        require('@/assets/club/bc/8ba38f461d52b3873e607a682ea6a7ba.webp'),
        require('@/assets/club/bc/cf1863aaedb12d6dfad4a60dfe23185c.webp'),
        require('@/assets/club/bc/DSC02670.webp'),
        require('@/assets/club/bc/DSC02674.webp'),
        require('@/assets/club/bc/DSC02687.webp'),
        require('@/assets/club/bc/DSC02690.webp'),
        require('@/assets/club/bc/DSC02691.webp'),
        require('@/assets/club/bc/DSC02695.webp'),
        require('@/assets/club/bc/DSC02709.webp'),
        require('@/assets/club/bc/DSC02712.webp'),
        require('@/assets/club/bc/DSC02714.webp'),
        require('@/assets/club/bc/DSC02748.webp'),
        require('@/assets/club/bc/DSC02754.webp'),
