      // 分批预加载，避免一次性加载过多图片导致卡顿
      const batchSize = 10;
      let currentBatch = 0;
      
      const loadBatch = () => {
        const start = currentBatch * batchSize;
        const end = Math.min(start + batchSize, photoUrls.length);
        
        for (let i = start; i < end; i++) {
          const img = new Image();
          img.src = photoUrls[i];
        }
        
        currentBatch++;
        if (currentBatch * batchSize < photoUrls.length) {
          // 延迟加载下一批，避免阻塞主线程
          setTimeout(loadBatch, 100);
        }
      };
      
      loadBatch();
    }
