  height: 100vh;
  position: relative;
  overflow: hidden;
  background: #111;
  margin-top: -80px;
  padding-top: 80px;
}

/* 加载覆盖层样式 */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #111;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.8s ease-in-out, transform 0.8s ease-in-out;
}

.loading-overlay.fade-out {
  opacity: 0;
  transform: scale(0.9);
}

.loading-content {
  text-align: center;
  color: #fff;
}

.dot-container {
  display: flex;
