  box-shadow: 0 0 10px #5f27cd;
}

.dot:nth-child(5) {
  background-color: #4ecdc4;
  animation-delay: 0.8s;
  box-shadow: 0 0 10px #4ecdc4;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
